veral pages thick at times - and patients are expected to comprehend and follow them. Most doctors remain largely disconnected from what is happening in patient’s homes and communities. ")]),_c('p',{staticClass:"pb-4"},[_c('span',{staticClass:"font-weight-bold"},[_vm._v("Health Care")]),_vm._v(" is prevention. It is a lifelong practice that doesn’t end when the patient leaves the clinic. It empowers people to make lifestyle changes that have a lasting physical and mental benefit. It goes beyond the individual and involves entire communities and cultures. It is wholistic and considers diet, exercise, support structures, stressors and mental states. ")]),_c('p',{staticClass:"pb-4"},[_vm._v(" Doctors Dean and Ayesha Sherzai created Healthy Minds Initiative in response to the helplessness they felt working at clinics, within the frameworks of traditional “sick care”. After watching all the latest drugs and treatments fail to stop dementia, Dean and Ayesha set out to discover a better preventative approach. ")]),_c('p',{staticClass:"pb-4"},[_vm._v(" Starting with local faith based groups and community centers, Dean and Ayesha began to investigate how communities could be empowered to make healthy lifestyle changes. They delved into the research on wholistic approaches and practiced them in their own daily lives. They looked at how technology could play a role in outreach, habit forming and intervention. ")]),_c('p',{staticClass:"pb-4"},[_vm._v(" Dean and Ayesha found that involving the community through the entire process - from investigation, to research, to intervention - empowered people to make significant lifestyle changes. Ultimately, these changes can significantly reduce diseases such as diabetes, high blood pressure, heart disease, strokes and dementia. ")]),_c('p',{staticClass:"pb-4"},[_vm._v(" Through HMI’s technology driven and community based approach, Dean and Ayesha continue to mobilize communities, fund new research, and spread the message that prevention